/**
 * Vendor registration table custom field JS
 *
 * @package YITH WooCommerce Multi Vendor
 * @since 4.0.0
 */

import AjaxRequest from './ajax-request';
import FieldsHandler from "./fields-handler";

export default class VendorRegistrationTable {

	field_handler;

	constructor() {
		this.init();
	}

	init() {
		this.table = jQuery( '.yith-vendor-registration-table-wrapper' );
		this.form = null;
		this.modal = null;

		if ( this.table.length ) {

			this.initSortable();

			this.table.on( 'click', '.yith-vendor-registration-table__add-fields', {self: this}, this.addField );
			this.table.on( 'click', '.yith-vendor-registration-table__edit-field', {self: this}, this.editField );
			this.table.on( 'click', '.yith-vendor-registration-table__delete-field', {self: this}, this.deleteField );
			this.table.on( 'click', '.yith-vendor-registration-table__restore-default', {self: this}, this.formReset );
			this.table.on( 'change', 'input[name="active"]', {self: this}, this.fieldActiveSwitch );
		}
	}

	initSortable() {
		const self 			= this,
			items_wrapper	= self.table.find('tbody'),
			items 			= items_wrapper.find( 'tr' );

		if ( items.length > 1 ) {
			items_wrapper.sortable({
				handle: '.yith-vendor-registration-table__drag-field',
				cursor: 'move',
				scrollSensitivity: 10,
				tolerance: 'pointer',
				axis: 'y',
				stop: function(event, ui) {
					let order = [];
					items_wrapper.find( 'tr' ).each( function() {
						order.push( jQuery(this).data('id') );
					});

					AjaxRequest.post(
						{
							request: 'registration_table_order_fields',
							order: order,
						},
						self.table
					);
				}
			}).disableSelection();
		}
	}

	modalActions() {
		if ( this.modal ) {

			this.form = this.modal.elements.content.find( '#vendor-registration-field-form' );

			this.form.on( 'keyup', '#name', this.sanitizeFieldName );
			this.form.on( 'change', '#name', {self:this}, this.checkDuplicatedName );
			// Handle options.
			this.initTableOptions();
			this.form.on( 'click', '#add_new_option', this.addNewOption.bind( this ) );
			this.form.on( 'click', '.options-table .delete', this.deleteOption );
			// Handle submit form.
			this.modal.elements.footer.on( 'click', '.vendor-registration-field-form-submit', {self: this}, this.formSubmit );
		}
	}

	sanitizeFieldName() {
		let value = jQuery( this ).val();
		// Format value.
		value = value.toLowerCase().replace( /[^0-9a-z-]+/, '-' );
		// Set new value.
		jQuery( this ).val( value );
	}

	checkDuplicatedName( event ) {
		const self = event.data.self;
		let value = jQuery(this).val();
		if ( value ) {
			if ( self.table.find('tr:not(.editing)').filter( 'tr[data-name="' + value + '"]' ).length ) {
				self.field_handler.addFieldError( jQuery(this).data( 'error' ), 'registration_form[name]' );
			} else {
				self.field_handler.resetFieldError( 'registration_form[name]' );
			}
		}
	}

	tableReplace( newTable ) {
		this.table.replaceWith( newTable );
		this.init(); // Refresh form.
	}

	openModal( data ) {

		const self = this,
			contentTemplate = wp.template( 'yith-wcmv-modal-registration-form' ),
			footerTemplate = wp.template( 'yith-wcmv-modal-registration-form-footer' );

		let tableRows = this.table.find( 'tr[data-name]' );

		this.modal = yith.ui.modal( {
			title: 'Add field',
			content: contentTemplate( data ),
			footer: footerTemplate(),
			width: 550,
			classes: {
				wrap: 'yith-vendor-registration-form-modal',
				content: 'yith-plugin-ui',
			},
			onCreate() {
				self.field_handler = new FieldsHandler( jQuery( '.yith-plugin-fw__modal__content .form-table' ) );
				self.field_handler.init();

				if ( ! jQuery.isEmptyObject( data ) ) {
					tableRows.filter('[data-name="' + data.name + '"]').addClass( 'editing' );
				}
			},
			onClose() {
				tableRows.removeClass( 'editing' );
			}
		} );

		this.modalActions();
	}

	addField( event ) {
		event.preventDefault();
		event.data.self.openModal( yith_wcmv_vendors.registrationTableFieldsDefault );
	}

	editField( event ) {
		event.preventDefault();

		let row = jQuery( this ).closest( 'tr' ),
			options = jQuery( this ).closest( 'tr' ).data( 'options' );

		if ( typeof options === 'undefined' ) {
			return;
		}

		options.options  = JSON.stringify( options.options ); // Stringify options.
		options.field_id = row.data('id'); // Add row ID.
		event.data.self.openModal( options );
	}

	deleteField( event ) {
		event.preventDefault();

		const self = event.data.self,
			field_id = jQuery( this ).closest( 'tr' ).data( 'id' );

		if ( ! field_id ) {
			return false;
		}

		yith.ui.confirm(
			{
				title: yith_wcmv_vendors.registrationDeleteFieldTitle,
				message: yith_wcmv_vendors.registrationDeleteFieldContent,
				confirmButtonType: 'delete',
				confirmButton: yith_wcmv_vendors.registrationDeleteFieldButton,
				closeAfterConfirm: true,
				onConfirm: function() {

					let data = {
						request: 'registration_table_field_delete',
						field_id: field_id
					}

					AjaxRequest.call( data, self.table, 'POST' ).done( ( res ) => {
						if ( res.success && res.data !== 'undefined' ) {
							self.tableReplace( res.data.html );
						}
					} );
				},
			}
		);
	}

	formSubmit( event ) {
		event.preventDefault();
		const self = event.data.self;

		// Trigger name check!
		self.form.find( '#name' ).trigger( 'change' );
		if ( self.field_handler.isFormWithErrors() ) {
			return false;
		}

		let data = self.form.serializeArray();

		// Add request param and do AJAX request
		data.push( {name: 'request', value: 'registration_table_field_save'} );

		self.modal.close();

		AjaxRequest.call( data, self.table, 'POST' ).done( ( res ) => {
			if ( res.success && res.data !== 'undefined' ) {
				self.tableReplace( res.data.html );
			}
		} );
	}

	fieldActiveSwitch( event ) {
		event.preventDefault();
		event.stopImmediatePropagation();

		const input = jQuery( this ),
			self = event.data.self;

		let data = {
			request: 'registration_table_field_active_switch',
			field_id: input.closest( 'tr' ).data( 'id' ),
			active: input.is( ':checked' ) ? 'yes' : 'no'
		}

		AjaxRequest.call( data, self.table, 'POST' ).done( ( res ) => {
			if ( res.success && res.data !== 'undefined' ) {
				self.tableReplace( res.data.html );
			}
		} );
	}

	formReset( event ) {
		event.preventDefault();

		const self = event.data.self;

		yith.ui.confirm(
			{
				title: yith_wcmv_vendors.registrationTableResetTitle,
				message: yith_wcmv_vendors.registrationTableResetContent,
				confirmButtonType: 'delete',
				confirmButton: yith_wcmv_vendors.registrationTableResetButton,
				closeAfterConfirm: true,
				onConfirm: function() {
					AjaxRequest.call( {request: 'registration_table_fields_reset'}, self.table, 'POST' ).done( ( res ) => {
						if ( res.success && res.data !== 'undefined' ) {
							self.tableReplace( res.data.html );
						}
					} );
				},
			}
		);
	}

	// OPTIONS HANDLER
	initTableOptions() {
		let table = this.form.find( 'table.options-table' ),
			value = table.data('value');
		// Add an empty row if there aren't any row.
		if ( typeof value !== 'object' ) {
			this.addOption();
		} else {
			let i = 0;
			jQuery.each( value, ( value, label ) => {
				this.addOption( i, value, label );
				i++;
			});
		}

		// Init sortable
		table.find( 'tbody').sortable({
			handle: '.drag',
			cursor: 'move',
			scrollSensitivity: 10,
			tolerance: 'pointer',
			axis: 'y',
		});
	}

	addNewOption( event ) {
		event.preventDefault();

		let index 	= 0;
		// Get the higher index value
		while ( this.form.find( 'table.options-table tr[data-index="'+ index +'"]' ).length ) {
			index++;
		}

		this.addOption( index );
	}

	addOption( index = 0, value = '', label = '' ) {

		let table 	= this.form.find( 'table.options-table' ),
			name    = table.data('name');

		table.find( 'tbody').append(
			'<tr data-index="'+ index +'">' +
			'<td class="column-label"><input type="text" name="'+ name +'['+ index +'][label]" id="options_'+ index +'_label" value="'+ label +'"></td>' +
			'<td class="column-value"><input type="text" name="'+ name +'['+ index +'][value]" id="options_'+ index +'_value" value="'+ value +'"></td>' +
			'<td class="column-actions"><span class="drag yith-icon yith-icon-drag ui-sortable-handle"></span><a href="#" role="button" class="delete yith-icon yith-icon-trash"></a></td>' +
			'</tr>'
		);
	}

	deleteOption( event ) {
		event.preventDefault();
		jQuery(this).closest('tr').remove();
	}
}
