/**
 * COMMISSION JAVASCRIPT HANDLER
 *
 * @package
 * @since 4.0.0
 */

import AjaxRequest from './ajax-request';
import VendorFieldsHandler from './vendor-fields-handler';

export default class VendorsTable {

	tableSelector = '#vendors-list-table';
	table = null;
	modal = null;
	modalType = null;
	fieldsHandler = null;

	constructor() {
		this.table = jQuery( this.tableSelector );
		this.init();
	}

	init() {

		jQuery( document ).on( 'click', '.create-vendor', {self: this}, this.create );

		if ( this.table.length ) {
			this.table.on( 'click', '.view-vendor a', this.view );
			this.table.on( 'click', '.edit-vendor', {self: this}, this.edit );
			this.table.on( 'click', '.delete-vendor', {self: this}, this.delete );
			this.table.on( 'click', '.approve-vendor', this.approve );
			this.table.on( 'click', '.vendor-enable-container .on_off', this.enable );
		}
	}

	initModalActions() {

		if ( null === this.modal ) {
			return false;
		}

		this.modal.elements.title.on( 'click', '.steps-list a', {self: this}, this.navToStep );

		this.modal.elements.content.on( 'click', '.owner-navigation a', {self: this}, this.vendorOwnerNav );
		this.modal.elements.content.on( 'click', '.vendor-field .set-password', this.showPasswordField );
		this.modal.elements.footer.on( 'click', '.vendor-next-step',  {self: this}, this.nextStep );
		this.modal.elements.footer.on( 'click', '.vendor-modal-submit', this.submitForm.bind( this ) );
	}

	create( event ) {
		event.preventDefault();
		event.data.self.openModal( {
			type: 'create',
			class: 'yith-wcmv-create-vendor-modal',
			content: yith_wcmv_vendors.createModalDefault
		} )
	}

	view( event ) {
		event.preventDefault();

		window.open( jQuery(this).attr('href'), '_blank' );
	}

	edit( event ) {
		event.preventDefault();

		const self = event.data.self;

		let trigger = jQuery( this ),
			vendor_id = trigger.data( 'vendor_id' ),
			data = {
				request: 'get-vendor-data',
				vendor_id: vendor_id
			};

		AjaxRequest.call( data, trigger.closest( 'td' ), 'GET' ).done( ( res ) => {
			if ( res.success ) {

				let data = {
					modalType: 'edit'
				};

				self.openModal( {
					type: 'edit',
					class: 'yith-wcmv-edit-vendor-modal',
					header: data,
					content: res.data !== 'undefined' ? jQuery.extend( {}, data, res.data ) : {}
				} );
			}
		} );

	}

	delete( event ) {
		event.preventDefault();

		const self = event.data.self;

		var trigger 	= jQuery( this ),
			vendor_id 	= trigger.data( 'vendor_id' ),
			vendor_name	= trigger.closest( 'tr' ).find( 'td.name' ).text();

		if ( ! vendor_id ) {
			return false;
		}

		yith.ui.confirm(
			{
				title: yith_wcmv_vendors.deleteModalTitle,
				message: yith_wcmv_vendors.deleteModalMessage.replace( '{{vendor_name}}', '<b>' + vendor_name + '</b>' ),
				confirmButtonType: 'delete',
				confirmButton: yith_wcmv_vendors.deleteModalButtonLabel,
				closeAfterConfirm: true,
				onConfirm: function() {

					let data = {
						request: 'delete-vendor',
						vendor_id: vendor_id
					};

					AjaxRequest.call( data, trigger.closest( 'td' ), 'POST' ).done( ( res ) => {
						if ( res.success ) {
							trigger.closest( 'tr' ).remove();

							if ( res.data && res.data.message ) {
								self.table.before( res.data.message ).show();
							}
						}
					} );
				},
			}
		);
	}

	approve() {
		let wrap = jQuery( this ).closest( 'td' ),
			request = jQuery( this ).data( 'request' ),
			data = {
				vendor_id: jQuery( this ).data( 'vendor_id' ),
				request: request,
			};

		AjaxRequest.call( data, wrap, 'POST' ).done( ( res ) => {
			if ( res.success ) {
				if ( request === 'reject-vendor' ) {
					wrap.closest( 'tr' ).remove();
				} else if ( request === 'approve-vendor' && res.data && res.data.html ) {
					wrap.html( res.data.html );
				}
			}
		} );
	}

	enable() {
		let input	= jQuery( this ),
			enabled = input.is( ':checked' ) ? 'yes' : 'no',
			vendor_id = input.data( 'vendor_id' );

		if ( vendor_id ) {
			let wrap = input.closest( 'td' ),
				data = {
					vendor_id: vendor_id,
					request: 'enable_vendor',
					enabled: enabled
				};

			AjaxRequest.call( data, wrap, 'POST' ).done( ( response ) => {
				if ( ! response?.success ) {
					window.onbeforeunload = ''; // Prevent window prompt.
					location.reload();
				}
			});
		}
	}

	openModal( data ) {

		const self = this;

		var defaults = {
			type: 'create',
			class: '',
			header: {},
			content: {},
			footer: {}
		};

		if ( null !== this.modal ) {
			return false;
		}

		const headerTemplate = wp.template( 'yith-wcmv-modal-vendor-modal-header' ),
			contentTemplate = wp.template( 'yith-wcmv-modal-vendor-modal-content' ),
			footerTemplate = wp.template( 'yith-wcmv-modal-vendor-modal-footer' );

		data = jQuery.extend( {}, defaults, data );

		self.modal = yith.ui.modal( {
			title: headerTemplate( data.header ),
			content: contentTemplate( data.content ),
			footer: footerTemplate( data.footer ),
			width: 1000,
			classes: {
				wrap: data.class,
				content: 'yith-plugin-ui',
			},
			closeSelector: '.vendor-close-modal',
			onCreate() {
				self.modalType = data.type;
				self.fieldsHandler = new VendorFieldsHandler( jQuery( '.yith-plugin-fw__modal__content' ) );
				self.fieldsHandler.init();
			},
			onClose() {
				self.modal = null;
				self.modalType = null;
			}
		} );

		self.initModalActions();

		jQuery( document ).trigger( 'yith_wcmv_vendors_modal_opened', [ self.modal, self.modalType ] );
	}

	async goToStep( stepID ) {

		let step = this.modal.elements.title.find( 'li[data-step="' + stepID + '"]' );
		if ( ! step.length ) {
			return false;
		}

		// Maybe create owner
		let owner = await this.maybeCreateOwner();
		if ( ! owner?.success ) {
			return false;
		}

		// Update Nav.
		step.removeClass( 'done' ).addClass( 'current' )
			.prevAll().removeClass( 'current' ).addClass( 'done' ).end()
			.nextAll().removeClass( 'current done' );

		// Update Content.
		this.modal.elements.content.find( 'fieldset[data-step="' + stepID + '"]' ).siblings( 'fieldset' ).hide().end().fadeIn();

		// If is modal create, update also footer buttons
		if ( 'create' === this.modalType ) {
			if ( ! step.next().length ) {
				this.modal.elements.footer.find( '.vendor-next-step' ).hide().end()
					.find( '.vendor-modal-submit' ).show();
			} else {
				this.modal.elements.footer.find( '.vendor-modal-submit' ).hide().end()
					.find( '.vendor-next-step' ).show();
			}
		}
	}

	navToStep( event ) {
		event.preventDefault();

		const self = event.data.self,
			trigger = jQuery( this );

		if ( self.isFormWithError() ) {
			return false;
		}

		// Nav only done steps for create modal.
		if ( 'create' === self.modalType && ! trigger.parent().hasClass( 'done' ) ) {
			return false;
		}

		self.goToStep( trigger.attr( 'href' ).replace( '#', '' ) );
	}

	getCurrentStep() {
		return this.modal.elements.title.find( '.steps-list li.current' ).data( 'step' );
	}

	nextStep( event ) {
		event.preventDefault();

		const self = event.data.self;

		let currentStep = self.getCurrentStep();
		if ( ! currentStep || self.isFormWithError() ) {
			return false;
		}

		let nextStep = self.modal.elements.title.find( 'li[data-step="' + currentStep + '"]' ).next().data( 'step' );
		if ( nextStep ) {
			self.goToStep( nextStep );
		}
	}

	vendorOwnerNav( event ) {
		event.preventDefault();

		let trigger = jQuery( this ),
			wrap 	= trigger.closest( '.vendor-owner-wrapper' ),
			dest 	= wrap.find( trigger.attr( 'href' ) );

		trigger.addClass( 'current' ).siblings().removeClass( 'current' );
		dest.fadeIn().siblings( ':not( .owner-navigation )' ).hide();

		event.data.self.fieldsHandler?.resetFormError( wrap );
	}

	maybeCreateOwner() {
		if ( ! this.modal.elements.content.find( '#create-owner' ).is( ':visible' ) ) {
			return {success:true};
		}

		let data = {};
		jQuery.each(
			this.modal.elements.content.find( 'form' ).serializeArray(),
			function( i, item ) {
				if ( 0 === item.name.indexOf( 'new_owner_' ) ) {
					data[item.name] = item.value;
				}
			}
		);
		data.request = 'create_owner';

		return AjaxRequest.post( data, this.modal.elements.content ).done( ( res ) => {

			let wrap = this.modal.elements.content.find( '#create-owner' );

			if ( res.success ) {
				// add new owner.
				let newOption = new Option( res.data.name, res.data.id, true, true );

				// Reset errors.
				this.fieldsHandler?.resetFormError( wrap );

				this.modal.elements.content.find( '.yith-wcmv-owner-select' ).append( newOption ).trigger( 'change' );
				this.modal.elements.content.find( '#create-owner' ).find( 'input' ).val( '' );
				this.modal.elements.content.find( '.owner-navigation a' ).first().click();
			} else {
				this.fieldsHandler?.addFieldError( res.data.message, res.data.field, wrap );
			}
		} );
	}

	showPasswordField( event ) {
		event.preventDefault();
		jQuery( this ).hide().next().fadeIn();
	}

	async submitForm() {
		let owner = await this.maybeCreateOwner();

		if ( owner?.success && ! this.isFormWithError() ) {
			if ( typeof jQuery.fn.block !== 'undefined' ) {
				this.modal.elements.main.block( {
					message: null,
					overlayCSS: {
						background: '#fff no-repeat center',
						opacity: 0.5,
						cursor: 'none',
					},
				} );
			}
			this.modal.elements.content.find( 'form' ).submit();
		}
	}

	// ERRORS HANDLER
	isFormWithError() {
		let wrap = this.modal !== null ? this.modal.elements.content : '';
		if ( ! wrap.length ) {
			return false;
		}
		// Check for single field.
		wrap.find( '.field-required' ).filter( ':visible' ).each( ( i, field ) => {
			if ( ! jQuery( field ).closest('.field-error').length && ! jQuery( field ).val() ) {
				this.fieldsHandler?.addFieldError( yith_vendors.requiredFieldError, jQuery( field ).attr('name') );
			}
		});

		let errors = wrap.find( '#error-message, .field-error' );
		if ( ! errors.length ) {
			return false;
		}

		let step = errors.last().closest('.step-content').attr('data-step');
		if ( step !== this.getCurrentStep ) {
			this.goToStep( step );
		}
		return true;
	}
}
